import { useCallback, useEffect, useRef, useState } from 'react';
import styled from 'styled-components';
import { GAME_DURATION } from '../../defines/Rule';
import TimeLeft from '../../components/TimeLeft';
import { useDispatch, useSelector } from 'react-redux';
import { ref, update } from 'firebase/database';
import { db } from '../../firebase/firebase';
import { setAuth } from '../../redux/authSlice';
import { useNavigate } from 'react-router-dom';

const WordDropGame = ({ WORDS }) => {
  const [words, setWords] = useState([]); //!< 화면에 보여지는 단어 state
  const [input, setInput] = useState(''); //!< input 단어 state
  const [score, setScore] = useState(0); //!< 점수 state
  const [gameOver, setGameOver] = useState(false); //!< GAME_DURATION 시간 종료 state
  const shapeId = useRef(0); //!< 화면에 같은 단어 나올수 있으니 단어 선택시 id부여
  const scoreRef = useRef(score); //!< useCallback 에서 값 기역하기 위해 의존성을 넣으면 게임시간이 초기화됨
  const authData = useSelector((state) => state.auth.data);
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const handleRetry = () => {
    // 게임 상태 초기화
    setScore(0);
    setWords([]);
    setGameOver(false);
  };

  //!< 단어 생성
  useEffect(() => {
    if (gameOver) return;
    const interval = setInterval(() => {
      const newWord = {
        id: shapeId.current++, //!< ID 부여 (같은 단어 일때 검사필요)
        text: WORDS[Math.floor(Math.random() * WORDS.length)], //!< WORD 에서 랜덤 단어 선택
        left: Math.random() * 80, //!< % 단위 위치
        top: 0, // 시작 위치
        speed: 0.5 + Math.random() * 1.0, //!< 떨어지는 속도 설정 (vh per tick)
      };
      setWords((prev) => [...prev, newWord]);
    }, 1500);

    //!< effect clean-up : interval break
    return () => clearInterval(interval);
  }, [gameOver]);

  // 단어 낙하 (top 증가)
  useEffect(() => {
    if (gameOver) return;

    const fallInterval = setInterval(() => {
      setWords(
        (prev) =>
          prev
            .map((w) => ({ ...w, top: w.top + w.speed })) //!< 단어이동 (현위치 + 속도 => 다음 위치)
            .filter((w) => w.top < 100) // 100vh 넘어가면 제거
      );
    }, 50);

    //!< effect clean-up: interval break
    return () => clearInterval(fallInterval);
  }, [gameOver]);

  const handleInputChange = (e) => {
    const val = e.target.value.trim().toLowerCase();
    setInput(val);
  };

  const onEnter = (e) => {
    e.preventDefault();

    //!< 입력한 값에서 화면 에 같은단어 있는지 검사(같은 단어 있을 때 먼저 만들어진거)
    const matched = words.find((w) => w.text === input);

    if (matched) {
      setScore((prev) => prev + input.length); //1< 단어 길이로 점수 계산
      setWords((prev) => prev.filter((w) => w.id !== matched.id)); //!< 단어 화면에서 제거
    }
    e.target.reset();
    setInput('');
  };

  useEffect(() => {
    scoreRef.current = score;
  }, [score]);

  const handleTimeout = useCallback(() => {
    if (scoreRef.current > authData.highScore) {
      const updates = {};
      updates[`/leaderboard/${authData.uid}/score`] = scoreRef.current;
      updates[`/users/${authData.uid}/highScore`] = scoreRef.current;
      update(ref(db), updates)
        .then(() => {
          console.log('리더보드 및 최고점수 업데이트 성공!');
          dispatch(setAuth({ ...authData, highScore: scoreRef.current }));
        })
        .catch((error) => {
          console.error('업데이트 실패:', error);
        });
    }

    setGameOver(true);
  }, []);

  return (
    <GameWrapper>
      {!gameOver ? (
        <>
          <HighScore>최고점수: {authData.highScore.toLocaleString()}</HighScore>
          <CurrScore>점수: {score.toLocaleString()}</CurrScore>
          <TimeLeft duration={GAME_DURATION} onTimeout={handleTimeout} />
          {words.map((word) => (
            <Word key={word.id} left={word.left} top={word.top}>
              {word.text}
            </Word>
          ))}
          <form onSubmit={onEnter}>
            <InputBox
              type="text"
              value={input}
              onChange={handleInputChange}
              autoFocus
              placeholder="단어를 입력하세요"
              disabled={gameOver}
            />
          </form>
        </>
      ) : (
        <GameOverScreen>
          <GameOverMessage>게임 종료!</GameOverMessage>
          <GameOverMessage>최종 점수: {score}</GameOverMessage>
          <Button onClick={handleRetry}>재도전</Button>
          <Button onClick={() => navigate('/menu')}>메뉴로 가기</Button>
        </GameOverScreen>
      )}
    </GameWrapper>
  );
};

export default WordDropGame;

const GameWrapper = styled.div`
  width: 100vw;
  height: 100vh;
  background: #222;
  position: relative;
  overflow: hidden;
`;

const HighScore = styled.div`
  position: absolute;
  top: 15px;
  left: 15px;
  color: white;
  font-size: 1.5rem;
  z-index: 10;
`;

const CurrScore = styled.div`
  position: absolute;
  top: 50px;
  left: 15px;
  color: white;
  font-size: 1.5rem;
  z-index: 10;
`;

const Word = styled.div`
  position: absolute;
  top: ${(props) => props.top}vh;
  left: ${(props) => props.left}vw;
  color: #ffd700;
  font-weight: bold;
  font-size: 1.5rem;
  user-select: none;
`;

const InputBox = styled.input`
  position: absolute;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  width: 50vw;
  max-width: 400px;
  padding: 10px 15px;
  font-size: 1.2rem;
  border-radius: 8px;
  border: none;
  outline: none;
`;

const GameOverScreen = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: #222;
  color: white;
  font-size: 2rem;
  gap: 1rem; /* 요소 간 간격 */
`;

const Button = styled.button`
  background-color: yellow;
  border: none;
  padding: 0.5rem 1.5rem;
  font-size: 1.2rem;
  cursor: pointer;
  border-radius: 8px;
  margin-top: 0.5rem;
`;

const GameOverMessage = styled.div`
  color: white;
  font-size: 2rem;
  /* position: absolute; */
  /* top: 50%;
  left: 50%; */
  /* transform: translate(-50%, -50%); */
`;
